<template>
	<div class="sub-header-affix none">
		<div class="header-affix-inner">
			<img src="/static/images/icon.png" class="affix-logo" />
			<ul>
				<li class="" @click="goAnchor"><a href="/"></a></li>
				<li class="wareAnchor" @click="goAnchor"></li>
				<li class="tAnchor" @click="goAnchor">T</li>
				<li class="dressAnchor" @click="goAnchor"></li>
				<li class="shirtAnchor" @click="goAnchor"></li>
				<li class="innerpathAnchor" @click="goAnchor"></li>
				<li class="knitwearAnchor" @click="goAnchor"></li>
				<li class="jeansAnchor" @click="goAnchor"></li>
				<li class="skirtAnchor" @click="goAnchor"></li>
				<li class="pathAnchor" @click="goAnchor"></li>
				<li class="fleeceAnchor" @click="goAnchor"></li>
				<li class="coatAnchor" @click="goAnchor"></li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		methods: {
			goAnchor(e) {
				var target = e.currentTarget.className;
				// $('#'+target).scrollTop = 0;
				$("html,body").animate({scrollTop:$("#" + target).offset().top - 50}, 300);
			}
		}
	}
</script>



